    async function getAsset() {
      var img = [];
      const node = create({
        host: "magentadao-backend.westeurope.cloudapp.azure.com",
        port: 443,
        protocol: "https",
      });

      const url = window.location.href;
      assetipfs = url.split("Qm")[1];
      if (typeof assetipfs == "undefined") {
        assetipfs = "bafybei" + url.split("bafybei")[1];
      } else {
        assetipfs = "Qm" + url.split("Qm")[1];
      }

      if (assetipfs === "bafybeiundefined") {
        window.location.assign("/#/error");
      } else {
        const source = node.cat(assetipfs);
        const data = [];
        for await (const chunk of source) {
          data.push(chunk);
        }
        const byteArray = data.toString().split(",");
        var json = "";
        for (var i = 0; i < byteArray.length; i++) {
          json += String.fromCharCode(byteArray[i]);
        }
        if (String(JSON.parse(json).image).includes("ipfs://")) {
          let assetipfs2 = "Qm" + JSON.parse(json).image.split("ipfs://Qm")[1];
          const source2 = node.cat(assetipfs2);
          const data2 = [];
          for await (const chunk2 of source2) {
            data2.push(chunk2);
          }
          const byteArray = data2.toString().split(",");
          var base64img = base64ArrayBuffer(byteArray);
          img.push("data:image/jpeg;base64," + base64img);
        } else {
          img = "";
        }
        const img_array = await JSON.parse(json).more_img;
        if (String(img_array).includes("ipfs://")) {
          const length = img_array.length;
          for (var y = 0; y < length; y++) {
            let assetipfs3 = "Qm" + img_array[y].split("ipfs://Qm")[1];
            const source3 = node.cat(assetipfs3);
            const data3 = [];
            for await (const chunk3 of source3) {
              data3.push(chunk3);
            }
            const byteArray = data3.toString().split(",");
            base64img = base64ArrayBuffer(byteArray);
            img.push("data:image/jpeg;base64," + base64img);
          }
        } else {
        }
        return [json, img];
      }
    }

